<template>
  <div class="sub_module">
    <div class="module_header">
      <div class="title">我的提问</div>
      <div class="more" v-if="dataList.length>0" @click="dialogVisible=true">
        查看详情
        <i class="iconfont icon-jiantou"></i>
      </div>
    </div>

    <el-row :gutter="20">
      <el-col :span="6" v-for="(item, index) in scorePreview" :key="index">
        <el-card shadow="hover" class="list_card blue_bg ">
          <div class="card_title">{{formatDate(item['CreateTime'], 'hh:mm:ss')}}</div>
          <p class="card_info"> {{item['QContent']}}</p>
        </el-card>
      </el-col>
    </el-row>

    <el-dialog title="提问记录明细" :visible.sync="dialogVisible">
      <!-- 提问记录 -->
      <div class="chat" ref="chat">

        <div v-if="dataList && dataList.length>0" class="chat_content">
          <div v-for="(sub, k) in dataList" :key="k" class=" chat_item">
            <div class="chat_news">
              <div class="chat_news_name">
                {{sub['StudentName']}}
                <span>{{formatDate(sub['CreateTime'], 'hh:mm:ss')}}</span>
              </div>
              <div class="chat_news_cont">
                <span>{{sub['QContent'] }}</span>
              </div>
            </div>
          </div>
        </div>

      </div>
    </el-dialog>
  </div>
</template>

<script>
  import commonTable from "@/components/commonTable";
  import { formatDate } from "@/utils/utils";

  export default {
    name: "StudentQuestion",
    components: { commonTable },
    data() {
      return {
        course: JSON.parse(sessionStorage.getItem("course")),
        courseID: sessionStorage.getItem("courseID"),
        dataList: [],
        listLoading: false,
        dialogVisible: false,
      };
    },
    created() {
      this.GetStudentQuestion();
    },
    methods: {
      formatDate(dateValue, format) {
        return formatDate(dateValue, format);
      },
      //获取学生提问
      GetStudentQuestion() {
        this.dataList = [];
        this.$http
          .get(this.$api.GetQuestionDatas, {
            courseID: this.courseID,
            studentID: this.$http.getStu().StudentID,
            attribute: 2 // 课中
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach((item, index) => {
                this.dataList.push(item);
              });
            }
          });
      }
    },
    computed: {
      scorePreview() {
        return this.dataList.filter((r, k) => k < 4);
      }
    }
  }
</script>

<style lang="less" scoped>
  .list_card {
    text-align: center;
    margin-bottom: 10px;

    .card_title {
      font-size: 12px;
      padding-bottom: 5px;
      color: #2a9efc;
    }

    .card_info {
      color: #2c3e50;
      background-color: #fff;
      border-radius: 6px;
      height: 70px;
      overflow-y: auto;
      word-break: break-all;
      word-wrap: break-word;

      font-size: 13px;
      padding-left: 10px;
      margin: 0;
      text-align: left;
    }

  }

  .chat {
    padding: 0 5px;
    text-align: left;
    width: 100%;
    max-height: 600px;
    overflow-y: auto;
    background: rgba(10, 10, 10, 0.1);
    border: 1px solid #f9f9f9;

    .chat_content {
      .chat_item {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;

        .chat_news {
          .chat_news_name {
            font-size: 12px;
            line-height: 2.5;
            color: #409EFF;
          }

          .chat_news_cont {
            padding: 6px 5px 6px 18px;
            font-size: 14px;
            line-height: 1.5;

            text-align: left;
            word-break: break-all;
            /*border: 1px solid #777;*/
            /*border-radius: 7px;*/

            img {
              max-width: 45vw;
              max-height: 25rem;
            }
          }
        }
      }

    }
  }

</style>



