<template>
  <div id="class-screen">

    <div class="content">
      <div class="head">

        <div class="mb">
          <div class="mb_title">
            <img :src="getPicture(cpdata.SSContent)" alt/>
          </div>
          <div class="mb">
            答案：
            <span class="primary">{{cpdata.SSAnswer}}</span>
          </div>
        </div>

        <div class="part_title">我的答题</div>
        <div class="part_line">{{saContent}}</div>

        <div class="part_title">问卷调研</div>
        <div class="part_line">{{saMaster}}</div>

      </div>
    </div>
  </div>
</template>

<script>
  import { ModulePicture } from "@/utils/enum";

  export default {
    props: {
      cpdata: {
        type: Object,
        default: () => {
          return {};
        }
      },
      subjectId: {
        type: String,
        default: ""
      }
    },
    data() {
      return {
        subjectType: "",
        saContent: "",
        saMaster: ""
      };
    },
    created() {
      this.getStudentAnswer();
    },
    methods: {
      getStudentAnswer() {
        this.$http
          .get(this.$api.ScreenQuestionStudentAnswer, {
            ssid: this.subjectId,
            studentID: this.$http.getStu().StudentID
          })
          .then(res => {
            if (res.Result && res.Data) {
              this.saContent = res.Data.SAContent;
              this.saMaster = res.Data.SAMaster == 0 ? "未掌握" : "已掌握";
            }
          });
      },

      getPicture(val) {
        return `${this.$api.InteractDownload}?filename=${val}&fileType=${ModulePicture.ScreenSubject}&platFormType=1`;
      }
    }
  };
</script>

<style lang='less' scoped>
  #class-screen {
    text-align: left;

    .content {
      width: 100%;

      .head {
        padding: 20px;
        background: rgba(255, 255, 255, 1);
        font-size: 14px;

        .part_title {
          border-left: 2px solid #2a9efc;
          padding-left: 5px;
          margin: 5px 0;
        }

        .part_line {
          border-bottom: 2px solid #f1f5f9;
          margin-top: 10px;
          padding-bottom: 10px;

          .el-row {
            padding-bottom: 10px;
          }

          .line_left {
            text-align: right;
            padding-right: 10px;
          }

          .line_right {
            padding-left: 10px;
          }
        }

        .mb {
          margin-bottom: 15px;

          .primary {
            color: #409eff;
          }

          .danger {
            color: #f56c6c;
          }
        }

        .mb_title {
          text-align: center;

          img {
            padding: 10px;
            max-width: 800px;
            max-height: 600px;
          }
        }
      }
    }
  }
</style>



