        <div class="ktzy">
          <div class="tt">课堂资源</div>
          <div class="bt zlbk">课件</div>
          <common-table :columns="coursewareColumns" :datas="courseWareDatas" th-class="table-innerhead" border
                        max-height="550" :select="false" :pagger="false" style="margin-bottom:20px;">
            <template slot="MaterialName" slot-scope="scope" align="center">
              {{scope.row.Value.MaterialName + '.'+scope.row.Value.MaterialFileExt}}
            </template>
            <template slot="CreateTime" slot-scope="scope" align="center">
              {{formatDate(scope.row.Key.CreateTime , 'yyyy-MM-dd hh:mm:ss')}}
            </template>
            <template slot="oprate" slot-scope="scope" align="center">
              <el-button type="success" size="mini" @click="clickDownload(scope.row)">下载</el-button>
              <el-button type="primary" size="mini" @click="clickComment(scope.row)">点评</el-button>
            </template>
          </common-table>
          <!--          <div class="nr">-->
          <!--            <div class="item" v-for="(item,index) in courseWareDatas" :key="index">-->
          <!--              <div class="ct">-->
          <!--                <img src="/static/courseware.png" alt style="height:80px;cursor:hand;"/>-->
          <!--                <div class="info">-->
          <!--                  <div class="name">{{item.Value.MaterialName + '.'+item.Value.MaterialFileExt}}</div>-->
          <!--                  <div class="time">{{formatDate(item.Key.CreateTime , 'yyyy-MM-dd hh:mm:ss')}}</div>-->
          <!--                </div>-->
          <!--              </div>-->
          <!--            </div>-->
          <!--          </div>-->

          <el-dialog title="课件评价" :visible.sync="dialogVisible" style="text-align: left;">
            <el-form ref="formComment" :model="formComment" :rules="rulesComment" size="small" label-width="80px">
              <el-form-item label="课件评分" prop="score">
                <el-input-number v-model="formComment.score" :min="1" :max="100" label="课件评分" controls-position="right"
                                 :precision="0" :step="1"></el-input-number>
                分
              </el-form-item>
              <el-form-item label="课件评价" prop="comment">
                <el-input type="textarea" v-model="formComment.comment" :autosize="{ minRows: 3, maxRows: 10}"
                          placeholder="请输入课件评价"
                          maxlength="500" :show-word-limit="true"></el-input>
              </el-form-item>
              <el-form-item label="" prop="comment">
                <el-checkbox v-model="formComment.anonymous">匿名评价</el-checkbox>
              </el-form-item>
              <div style="text-align:right;">
                <el-button @click="dialogVisible = false">取消</el-button>
                <el-button :loading="btnLoading" type="primary" @click="saveForm('formComment')">
                  {{formComment.id ? '更新评价' : '保存' }}
                </el-button>
              </div>
            </el-form>
          </el-dialog>

          <div class="bt zlbk">板书</div>
          <div class="nr">
            <div class="item" v-for="(item,index) in boardNoteDatas" :key="index" :item="item">
              <div class="ct">
                <div class="cdiv">
                  <el-popover trigger="click">
                    <img :src="item.img" alt="" :style="{maxWidth:'800px', maxHeight:'500px'}"/>
                    <img :src="item.img" slot="reference" alt class="cimg"/>
                  </el-popover>
                </div>
                <div class="info">
                  <div class="time">{{formatDate(item.CreateTime, 'yyyy-MM-dd hh:mm:ss')}}</div>
                </div>
              </div>
            </div>
          </div>

          <div class="bt zlbk">小组笔记</div>
          <div class="nr">
            <div class="item" v-for="(item,index) in groupNoteDatas" :key="index" :item="item">
              <div class="ct">
                <div class="cdiv">
                  <el-popover trigger="click">
                    <img :src="item.img" alt="" :style="{maxWidth:'800px', maxHeight:'500px'}"/>
                    <img :src="item.img" slot="reference" alt class="cimg"/>
                  </el-popover>
                </div>
                <div class="info">
                  <div class="time">{{formatDate(item.CreateTime, 'yyyy-MM-dd hh:mm:ss')}}</div>
                </div>
              </div>
            </div>
          </div>

          <div class="bt zlbk">课堂实况</div>
          <div v-for="(item, index) in microLessonDatas" :key="index">
            <i class="el-icon-video-camera-solid title_first"></i>
            {{item.type==='teacher'?'老师实况':'小组实况'}}
            <div v-for="(sub, k) in item.List" :key="k">
              <i class="el-icon-s-flag title_second"></i>
              {{sub.mark ? sub.mark : '无标记'}}
              <el-row :gutter="20">
                <el-col :span="8" v-for="(sec, i) in sub.List" :key="i"
                        :style="{textAlign: 'center', lineHeight:2}">
                  <!--                  <p>{{sec['MLTitle']||'微课视频文件'}}</p>-->
                  <div class="video-player">
                    <video :ref="'video'+i" preload="false" controls="controls" :poster="sec.poster" :src="sec.src"
                           @canplaythrough="sec.isLoading = false" @error="handleError($event, i)"
                           @stalled="sec.isLoading = true" @waiting="sec.isLoading = true"
                           @playing="vedioChange($event, i)">
                      您的浏览器不支持HTML5
                    </video>
                    <div class="loader" v-show="sec.isLoading">
                      <div class="loader-inner ball-clip-rotate">
                        <div></div>
                      </div>
                    </div>
                    <!-- 错误弹窗 -->
                    <div class="error" v-show="sec.isError">
                      <p class="lose">视频加载失败</p>
                      <p class="retry" @click="retry($event, index)">点击重试</p>
                    </div>
                  </div>
                  <div>{{sec['MLTitle']||'微课视频文件'}}</div>
                </el-col>
              </el-row>
            </div>

          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import commonTable from "@/components/commonTable";
  import defaultBg from "./../../../../../../static/video.png";
  import courseInfo from "./../../../../../components/personal/teacher/myCourse/courseInfo.vue";
  import { formatDate } from "@/utils/utils";
  import { ModulePicture } from "@/utils/enum";
  import ScreenQuestion from "./ScreenQuestion.vue";
  import StudentQuestion from "./StudentQuestion.vue";
  import ClassPractice from "./ClassPractice.vue";
  import RandomPerson from "./../../../teacher/myCourse/classRecord/component/RandomPerson";
  import GroupDiscussion from "./GroupDiscussion.vue";

  export default {
    name: "",
    components: {
      commonTable,
      courseInfo,
      ScreenQuestion,
      StudentQuestion,
      ClassPractice,
      RandomPerson,
      GroupDiscussion,
    },
    data() {
      return {
        course: JSON.parse(sessionStorage.getItem("course")),
        courseID: sessionStorage.getItem("courseID"),
        interactConfig: "",
        teacherComment: null,
        signStateName: "",
        signTime: "",
        paScore: 0,
        gdScore: 0,
        saScore: 0,
        totalScore: 0,
        groupingDatas: [],
        coursewareColumns: [
          { prop: "MaterialName", label: "课件", slot: true },
          { prop: "CreateTime", label: "创建时间", slot: true },
          { prop: "oprate", label: "操作", slot: true, width: 160 }
        ],
        courseWareDatas: [],
        boardNoteDatas: [],
        groupNoteDatas: [],
        microLessonDatas: [],

        checked: false,
        kqDetail: [
          {
            name: "学生"
          }
        ],

        vedioIndex: -1,

        dialogVisible: false,
        formComment: {
          id: "",
          courseID: "",
          coursewareID: "",
          comment: "",
          anonymous: false,
          score: 100
        },
        rulesComment: {
          comment: {
            required: true,
            min: 1,
            max: 500,
            message: "长度在 1 到 500 个字符",
            trigger: "blur"
          },
          score: [
            { required: true, message: "课程评分不能为空" },
            { type: "number", message: "课程评分必须为数字值" }
          ]
        },
        btnLoading: false
      };
    },
    computed: {},
    watch: {},
    created() {
      this.getTeacherEvaluate();
      this.GetSignData();
      this.GetStudentScore();
      this.GetInteractConfig();
      this.GetGroupingDatas();
      this.GetCourseWareDatas();
      this.GetBoardNoteDatas();
      this.GetGroupNoteDatas();
    },
    mounted() {
    },
    methods: {
      formatDate(dateValue, format) {
        return formatDate(dateValue, format);
      },
      // 老师点评
      getTeacherEvaluate() {
        let reqData = {
          courseID: this.courseID,
          studentID: this.$http.getStu().StudentID
        };
        this.$http.get(this.$api.CourseCommentDataByTeacher, reqData)
          .then(res => {
            if (res.Result && res.Data) {
              this.teacherComment = res.Data;
            }
          });
      },

      //获取签到
      GetSignData() {
        this.$http
          .get(this.$api.GetSignData, { courseID: this.courseID, studentID: this.$http.getStu().StudentID })
          .then(res => {
            if (res.Result) {
              this.signTime = res.Data.SignTime;
              switch (res.Data.SignState) {
                case 0:
                  this.signStateName = "旷课";
                  break;
                case 1:
                  this.signStateName = "正常";
                  break;
                case 2:
                  this.signStateName = "迟到";
                  break;
                case 3:
                  this.signStateName = "请假";
                  break;
              }
            } else {
              this.signStateName = "旷课";
            }
          });
      },

      //获取学生得分
      GetStudentScore() {
        this.$http
          .get(this.$api.GetStudentScore, { courseID: this.courseID, studentID: this.$http.getStu().StudentID })
          .then(res => {
            if (res.Result) {
              this.paScore = res.Data.PAScore;
              this.gdScore = res.Data.GDScore;
              this.saScore = res.Data.SAScore;
              this.totalScore = res.Data.TotalScore;
            }
          });
      },

      //获取智慧教室配置
      GetInteractConfig() {
        this.$http
          .get(this.$api.GetInteractConfig, { clsRoomID: this.course.ClassroomID })
          .then(res => {
            if (res.Result) {
              this.interactConfig = JSON.parse(res.Data.ConfigInfo);

              this.GetMicroLessonDatas();
            }
          });
      },

      //获取图片
      fetchPic(filename, fileType) {
        return this.$api.InteractDownload + "?fileName=" + filename + "&fileType=" + fileType + "&platFormType=1";
      },

      //获取分组次数
      GetGroupingDatas() {
        this.$http
          .get(this.$api.GetGroupingDatas, { courseID: this.courseID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.groupingDatas.push(item);
              });
            }
          });
      },

      //获取课件
      GetCourseWareDatas() {
        this.$http
          .get(this.$api.CourseWareList, { courseID: this.courseID, attribute: 2 })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.courseWareDatas.push(item);
              });
            }
          });
      },

      //获取板书
      GetBoardNoteDatas() {
        this.$http
          .get(this.$api.GetBoardNoteDatas, { courseID: this.courseID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.boardNoteDatas.push({ ...item, img: this.fetchPic(item["BNContent"], ModulePicture.BoardNote) });
              });
            }
          });
      },

      //获取小组笔记
      GetGroupNoteDatas() {
        this.$http
          .get(this.$api.groupNoteDatas, { courseID: this.courseID, studentID: this.$http.getStu().StudentID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.groupNoteDatas.push({ ...item, img: this.fetchPic(item["GNContent"], ModulePicture.groupNote) });
              });
            }
          });
      },

      //获取微课
      async GetMicroLessonDatas() {
        this.microLessonDatas = [];
        let teacherTypes = [1, 2, 3, 4];
        let teachers = [];
        let students = [];
        let vedioUrl = "http://" + this.interactConfig.RecIP + ":50120";
        this.$http
          .get(this.$api.GetMicroLessonDatas, { courseID: this.courseID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                let mark = item["RecMark"] + "";
                let sub = {
                  ...item,
                  src: vedioUrl + "/" + item["MLID"] + "." + item["FileFormat"],
                  isLoading: false,
                  isError: false,
                  poster: defaultBg
                };
                if (teacherTypes.indexOf(item['MLType']) > -1) {
                  let index = teachers.findIndex(r => r.mark === mark);
                  if (index > -1) {
                    teachers[index].List.push(sub)
                  } else {
                    teachers.push({
                      mark: mark,
                      List: [sub]
                    })
                  }
                } else {
                  let index = students.findIndex(r => r.mark === mark);
                  if (index > -1) {
                    students[index].List.push(sub)
                  } else {
                    students.push({
                      mark: mark,
                      List: [sub]
                    })
                  }
                }

                // this.microLessonDatas.push({
                //   ...item,
                //   src: vedioUrl + "/" + item["MLID"] + "." + item["FileFormat"],
                //   isLoading: false,
                //   isError: false,
                //   poster: defaultBg
                // });
              });
              if (teachers.length > 0) {
                this.microLessonDatas.push({ type: 'teacher', List: teachers });
              }
              if (students.length > 0) {
                this.microLessonDatas.push({ type: 'student', List: students });
              }
              console.log(this.microLessonDatas);
            }
          });
      },

      //切换微课视频
      vedioChange(e, index) {
        if (this.vedioIndex !== -1) {
          if (this.vedioIndex !== index) {
            if (this.vedioIndex !== -1) this.$refs["video" + this.vedioIndex][0].load();
            this.vedioIndex = index;
          }
        } else {
          this.vedioIndex = index;
        }
      },

      //数据加载出错
      handleError(e, index) {
        this.microLessonDatas[index].isLoading = false;
        this.microLessonDatas[index].isError = true;
      },

      //点击重新加载
      retry(e, index) {
        this.microLessonDatas[index].isError = false;
        setTimeout(() => {
          this.vedioIndex = index;
          if (this.vedioIndex !== -1) this.$refs["video" + this.vedioIndex][0].load();
        }, 200);
      },

      // 课件下载
      clickDownload(row) {
        this.$http
          .get(this.$api.getMaterialDataByID, {
            ids: row.Key["CID"],
            Token: this.$http.getToken()
          })
          .then(res => {
            if (res.Result) {
              let downurl = this.$api.DownloadFileByMIDURL;
              window.location.href = downurl + "?mid=" + row.Key["MaterialID"];
              // this.AddStuCoursewareTask(row.Key["CID"], row.Key["MaterialID"]);
            }
          })
          .catch(err => {
          });
      },
      // 课件点评
      clickComment(row) {
        this.formComment = {
          id: "",
          courseID: row.Key["CourseID"],
          coursewareID: row.Key["CID"],
          studentID: this.$http.getStu().StudentID,
          comment: "",
          anonymous: false,
          score: 100
        };
        this.getCourseWareEvaluate();
        this.dialogVisible = true;
      },
      getCourseWareEvaluate() {
        let reqData = {
          courseID: this.formComment.courseID,
          studentID: this.$http.getStu().StudentID,
          cID: this.formComment.coursewareID
        };
        this.$http
          .get(this.$api.CoursewareCommentData, reqData)
          .then(res => {
            if (res.Result && res.Data) {
              this.formComment.id = res.Data["CCID"];
              this.formComment.comment = res.Data["Comment"];
              this.formComment.score = res.Data["Score"];
              this.formComment.anonymous = res.Data["IsAnonymous"] ? true : false;
            }
          });
      },
      saveForm(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.btnLoading = true;
            let reqData = {
              ccID: this.formComment.id,
              courseID: this.formComment.courseID,
              cID: this.formComment.coursewareID,
              studentID: this.$http.getStu().StudentID,
              score: this.formComment.score,
              comment: this.formComment.comment,
              isAnonymous: this.formComment.anonymous ? 1 : 0,
              type: 2
            };
            this.$http
              .post(this.$api.CoursewareCommentSave, reqData)
              .then(res => {
                if (res.Result) {
                  this.dialogVisible = false;
                  this.$message({
                    message: "提交成功！",
                    type: "success"
                  });
                } else if (res.message) {
                  this.$message({
                    showClose: true,
                    ...res
                  });
                }
                this.btnLoading = false;
              })
              .catch(err => {
                this.btnLoading = false;
              });
          } else {
            this.$message({
              showClose: true,
              message: "请输入正确格式",
              type: "error"
            });
            return false;
          }
        });
      },
    }
  };
</script>

