<template>
  <div class="sub_module">
    <div class="module_header">
      <div class="title">随堂练习</div>
    </div>

    <common-table :columns="tableColumns" :datas="dataList" th-class="table-innerhead" border :pagger="false"
                  max-height="550"
                  v-loading="dataLoading">
      <template slot="oprate" slot-scope="scope" align="center">
        <el-button type="success" size="mini" @click="getPracticeDetail(scope.row)">查看答题</el-button>
      </template>
    </common-table>

    <el-dialog class="cp_detail" title="答题明细" :visible.sync="dialogDetailVisible">
        <div class="mb" v-for="(item, index) in cpDetail" :key="index">
            <div class="mb">{{item.PSData.PSSerial}}. [{{fetchPSType(item.PSData.PSType)}}] {{item.PSData.PSScore}}分</div>
            <div class="mb">{{item.PSData.PSContent}}</div>
            <div class="mb" v-if="item.PSData.PSType===1 || item.PSData.PSType===2">
                <div v-for="(soItem, index) in item.SOData" :key="index" :item="soItem">{{soItem.SOSerial}}. {{soItem.SOContent}}</div>
            </div>
            <div class="mb">
                答案：
                <span class="primary">{{item.PSData.PSAnswer}}</span>
            </div>
            <div class="mb">
                解析：
                <span class="danger">{{item.PSData.PSanalysis}}</span>
            </div>

            <div class="part_title">我的答题</div> 
            <div v-html="item.PAData">  
            </div>
        </div>
    </el-dialog>

  </div>
</template>

<script>
  import commonTable from "@/components/commonTable";
  import api from "../../../../../utils/api"; 
  export default {
    name: "ClassPractice",
    components: { commonTable },
    data() {
      return {
        course: JSON.parse(sessionStorage.getItem("course")),
        courseID: sessionStorage.getItem("courseID"),
        dataList: [],
        dataLoading: false,
        tableColumns: [
          { prop: "CPTitle", label: "名称", width: 300 },
          { prop: "SubjectTotal", label: "题数" },
          { prop: "SubjectScore", label: "总分" },
          { prop: "CPTime", label: "计时(分)" },
          { prop: "oprate", label: "操作", slot: true, width: 150 }
        ],

        dialogDetailVisible: false,
        cpDetail: []
      }
    },
    created() {
      this.getClassPracticeStats();
    },
    methods: {
      //获取随堂练习
      getClassPracticeStats() {
        this.dataLoading = true;
        this.dataList = [];
        this.$http
          .get(this.$api.ClassPractiseStatistics, { courseID: this.courseID, attribute: 2 })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.dataList.push({
                  CPID: item.Key.CPID,
                  PLID: item.Key.PLID,
                  CPTitle: item.PLTitle,
                  SubjectTotal: item.Total,
                  SubjectScore: item.Score,
                  CPTime: item.Key.CPTime,
                  CreateTime: item.Key.CreateTime,
                  State: item.Key.State
                });
              });
            }
            this.dataLoading = false;
          }).catch(e => {
          this.dataLoading = false;
        });
      },

      //获取学生答题
     async getPracticeDetail(row) {
          this.cpDetail = [];
          this.dialogDetailVisible = true;
          let downloadURL = await api.GetI_DownloadFileURL();
          this.$http
              .get(this.$api.GetCPStudentAnswer, { cpID: row.CPID, studentID: this.$http.getStu().StudentID })
              .then(res => {
                  if (res.Result && res.Datas && res.Datas.length > 0) { 
                      res.Datas.forEach(item => {
                        var newContent = "未作答";
                            if(item.PAData != null) {
                                var htmstr = item.PAData.PAContent;
                                newContent = htmstr.replace(
                                /<img [^>]*src=['"]([^'"]+)[^>]*>/gi,
                                function(match, capture) {  
                                var filename = capture.split('/')[1]; 
                                var newStr = '<img src="' + downloadURL + '?filename=' + filename + '&fileType=7" alt="" style="width:380px"/>';
                                return newStr;
                                }
                            ); 
                            }  
                          this.cpDetail.push({
                              PSData: item.PSData,
                              PAData: newContent,
                              SOData: item.SOList
                          }); 
                      });
                  }
              })
              .catch(err => {
                  
              });
      },

      //获取题目类型
      fetchPSType(val) {
          let typeName = "";
          switch (val) {
          case 1:
              typeName = "单选题";
              break;
          case 2:
              typeName = "多选题";
              break;
          case 3:
              typeName = "判断题";
              break;
          case 4:
              typeName = "问答题";
              break;
          }

          return typeName;
      }
    }
  }
</script>

<style lang='less'>

</style>



