/**
 * 截屏提问类型名称
 * @param {*} val
 */
export function screenQuestionTypeName(val) {
  if (val === 1) {
    return "单选题";
  } else if (val === 2) {
    return "多选题";
  } else if (val === 3) {
    return "判断题";
  } else if (val === 4) {
    return "问答题";
  } else {
    return "未知题型";
  }
}

/**
 * 截屏提问模式名称
 * @param {*} val
 */
export function screenPatternName(val) {
  if (val === 1) {
    return "全员作答";
  } else if (val === 2) {
    return "随机挑人";
  } else if (val === 3) {
    return "抢答模式";
  } else {
    return "其他";
  }
}

/**
 * 截屏提问状态
 */
export function ScreenSubjectStateName(val) {
  if (val === 1) {
    return "进行中";
  } else if (val === 2) {
    return "结束答题";
  } else if (val === 3) {
    return "停止答题";
  } else {
    return "未知";
  }
}

/**
 * 课程属性
 * @type {Array}
 */
export const classProperty = [
  { label: "课前", type: "before", value: "1" },
  { label: "课中", type: "in", value: "2" },
  { label: "课后", type: "after", value: "3" },
];



