<template>
  <div class="sub_module">

    <div class="module_header">
      <div class="title">截屏提问</div>
      <div class="more" @click="initDialog()">
        查看详情
        <i class="iconfont icon-jiantou"></i>
      </div>
    </div>

    <el-row :gutter="20" v-loading="dataLoading">
      <el-col :span="6" v-for="(item, index) in dataList" :key="index">
        <el-card shadow="hover" class="list_card blue_bg">
          <div class="card_info" @click="initDialog(item['Type'])">
            <div>{{item.name}}</div>
            <div class="card_line">
              <div> 题数：{{item['Total']}}</div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <el-dialog title="截屏提问列表" :visible.sync="dialogVisible">

      <el-tabs type="border-card" v-model="selectType" @tab-click="selectTypeChange">
        <el-tab-pane v-for="(item, index) in dataList" :key="index" :label="item.name" :name="item['Type'] + ''">
          <keep-alive>
            <el-table :data="typeDataList" max-height="550" v-loading="listLoading" stripe type="expand"
                      :default-sort="{prop: 'SSSerial', order: 'ascending'}">
              <el-table-column type="expand">
                <template slot-scope="props">
                  <screen-detail :cpdata="props.row" :subjectId="props.row['SSID']+''"></screen-detail>
                </template>
              </el-table-column>
              <el-table-column property="SSSerial" label="题号">
              </el-table-column>

              <el-table-column property="SSPatternName" label="模式">
                <template slot-scope="scope">
                  <span>{{getPatternName(scope.row.SSPattern)}}</span>
                </template>
              </el-table-column>

              <el-table-column property="SSScore" label="总分">
              </el-table-column>

              <el-table-column property="SSTime" label="计时(分)">
              </el-table-column>

              <el-table-column property="CreateTime" label="时间" width="200px">
                <template slot-scope="scope">
                  <span>{{formatDate(scope.row.CreateTime, 'yyyy-MM-dd hh:mm:ss')}}</span>
                </template>
              </el-table-column>

              <el-table-column property="State" label="状态">
                <template slot-scope="scope">
                  <span>{{getStateName(scope.row.State)}}</span>
                </template>
              </el-table-column>

            </el-table>
          </keep-alive>
        </el-tab-pane>
      </el-tabs>

    </el-dialog>
  </div>
</template>

<script>
  // import commonTable from "@/components/commonTable";
  import screenDetail from "./ScreenDetail";
  import { formatDate } from "@/utils/utils";
  import { screenQuestionTypeName, screenPatternName, ScreenSubjectStateName } from "@/utils/enum";

  export default {
    name: "ScreenQuestion",
    components: { screenDetail },
    data() {
      return {
        course: JSON.parse(sessionStorage.getItem("course")),
        courseID: sessionStorage.getItem("courseID"),
        dataList: [],
        dataLoading: false,

        dialogVisible: false,
        selectType: null,
        listLoading: false,
        typeDataList: []
      }
    },
    created() {
      this.GetScreenQuestionStats();
    },
    methods: {
      //获取截屏提问类型统计
      GetScreenQuestionStats() {
        this.dataLoading = true;
        this.dataList = [];
        this.$http
          .get(this.$api.GetGroupByTypeStats, { courseID: this.courseID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.dataList.push({
                  ...item,
                  name: screenQuestionTypeName(item["Type"])
                });
              });
            }
            this.dataLoading = false;
          }).catch(e => {
          this.dataLoading = false;
        });
      },
      initDialog(type) {
        if (type) {
          this.selectType = type + "";
        } else if (this.dataList.length > 0) {
          this.selectType = this.dataList[0]["Type"] + "";
        }
        this.selectTypeChange();
        this.dialogVisible = true;
      },
      // 切换题型
      selectTypeChange() {
        this.listLoading = true;
        this.typeDataList = [];
        this.$http
          .get(this.$api.GetSSDatas, { courseID: this.course.CourseID, ssType: this.selectType })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.typeDataList.push(item);
              });
            }
            this.listLoading = false;
          }).catch(e => {
          this.listLoading = false;
        });
      },

      getPatternName(val) {
        return screenPatternName(val);
      },
      formatDate(dateValue, format) {
        return formatDate(dateValue, format);
      },
      getStateName(val) {
        return ScreenSubjectStateName(val);
      },
    }
  }
</script>

<style lang="less" scoped>
  .list_card {
    text-align: left;

    .card_info {
      color: #2c3e50;
      line-height: 2.5;

      .card_line {
        /*display: flex;*/
        /*justify-content: space-between;*/
        font-size: 14px;
      }
    }

  }
</style>



