<template>
  <div class="sub_module">
    <div class="module_header">
      <div class="title">随机挑人</div>
    </div>

    <el-row :gutter="20" v-loading="randomPersonLoading">
      <el-col :span="8" v-for="(item, index) in randomPersonList" :key="index" :style="{padding:'10px'}">
        <el-card shadow="hover" class="list_card blue_bg">
          <div slot="header" class="card_title">
            <span>第{{ item['Key']['RPSerial']}}次随机挑人</span>
          </div>
          <el-row :gutter="10" class="card_person">
            <el-col :span="6" v-for="(subItem,subIndex) in item.Value" :key="subIndex" :style="{padding:'5px'}">
              <img src="/static/boy_img.png" alt=""/>
              <div>{{subItem.StudentName}}</div>
            </el-col>
          </el-row>
        </el-card>
      </el-col>
    </el-row>
  </div>

</template>

<script>
  export default {
    name: "ClassPractice",
    data() {
      return {
        course: JSON.parse(sessionStorage.getItem("course")),
        courseID: sessionStorage.getItem("courseID"),
        randomPersonList: [],
        randomPersonLoading: false
      }
    },
    created() {
      this.getRandomPersonList();
    },
    methods: {
      //获取随机挑人
      getRandomPersonList() {
        this.randomPersonLoading = true;
        this.randomPersonList = [];
        this.$http
          .get(this.$api.GetRPDatas, { courseID: this.courseID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.randomPersonList.push(item);
              });
            }
            this.randomPersonLoading = false;
          }).catch(e => {
          this.randomPersonLoading = false;
        });
      },
    },
  }
</script>

<style lang="less" scoped>
  .list_card {
    text-align: center;

    /deep/ .el-card__header {
      padding: 0;

      .card_title {
        padding: 12px 20px;
        background: rgba(42, 158, 252, 0.7);
        color: #fff;
        font-size: 15px;
      }
    }

    .card_person {
      text-align: center;
      font-size: 12px;
      max-height: 84px;
      overflow-y: auto;
      img{
        max-width: 55px;
        max-height: 55px;
      }
    }

  }
</style>



