<template>
  <div class="sub_module">
    <div class="module_header">
      <div class="title">小组讨论</div>
    </div>

    <el-table :data="subjectList" v-loading="discussSubjectLoading" stripe type="expand"
              :default-sort="{prop: 'subject.createTime', order: 'descending'}">
      <el-table-column type="expand">
        <template slot-scope="props">
          <common-table :columns="tableColumns" :datas="props.row.groupList"
                        th-class="table-innerhead" border :select="false" :pagger="false">
            <template slot="oprate" slot-scope="scope">
              <el-button type="primary" size="small" @click="viewDetail(scope.row, props.row.subject)">查看明细</el-button>
            </template>
          </common-table>
        </template>
      </el-table-column>
      <el-table-column property="subject.gsSort" label="话题序号">
      </el-table-column>
      <el-table-column property="subject.content" label="讨论题目">
        <template slot-scope="scope">
          <span v-if="scope.row.subject.type===1">{{ scope.row.subject.content}}</span>
          <el-popover v-else trigger="click">
            <img :src="scope.row.subject.img" alt=""/>
            <img class="table_img" slot="reference" :src="scope.row.subject.img" alt=""/>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column property="subject.time" label="讨论时长(分)">
        <template slot-scope="props">
          <span v-if="props.row.subject.time">{{props.row.subject.time}}</span>
          <span v-else>无限制</span>
        </template>
      </el-table-column>
      <el-table-column property="subject.createTime" label="创建时间">
        <template slot-scope="scope">
          <span>{{formatDate(scope.row.subject.createTime, 'yyyy-MM-dd hh:mm:ss')}}</span>
        </template>
      </el-table-column>
      <el-table-column property="subject.state" label="状态">
      </el-table-column>
    </el-table>

    <el-dialog title="小组讨论记录" :visible.sync="dialogVisible">
      <el-row>
        <el-col :span="18">

          <!-- 头部摘要 -->
          <div class="summary">
            <div> 总得分: <span>{{score}}</span></div>
            <div> 总点赞: <span>{{good}}</span></div>
            <div> 讨论时长: <span>{{dataInfo.time === 0 ? '无限制' : dataInfo.time}}</span></div>
          </div>

          <!-- 聊天信息 -->
          <div class="chat" ref="chat">
            <div class="chat_title">
              <span class="chat_title_left">[论题]</span>
              <span v-if="dataInfo.type === contentTypeDefault ">{{ dataInfo.content }}</span>

              <el-popover v-else trigger="click">
                <img :src="dataInfo.img" alt="" :style="{maxWidth:'800px',maxHeight:'500px'}"/>
                <img :src="dataInfo.img" slot="reference" :style="{maxWidth:'350px',maxHeight:'150px'}" alt=""/>
              </el-popover>
            </div>

            <div v-if="dataList && dataList.length>0" class="chat_content">
              <div v-for="(sub, k) in dataList" :key="k"
                   :class=" sub.studentId!== selectStudentId ? 'chat_item' : 'chat_item checked_item'">
                <div class="chat_news">

                  <div class="chat_news_name">
                    {{sub.name}}
                    <span>{{sub.time}}</span>
                    <span class="chat_comment" v-if="sub.score">+{{sub.score||0}}分</span>
                  </div>

                  <el-badge type="warning" :value="sub.thumb > 0 ? '赞' : ''">
                    <div class="chat_news_cont">
                      <span v-if="sub.type === contentTypeDefault">{{sub.postContent }}</span>
                      <el-popover v-else trigger="click">
                        <img :src="sub.postImg" alt="" :style="{maxWidth:'800px',maxHeight:'500px'}" />
                        <img :src="sub.postImg" slot="reference" :style="{maxWidth:'350px',maxHeight:'150px'}" alt=""/>
                      </el-popover>
                    </div>
                  </el-badge>
                </div>
              </div>
            </div>

          </div>
        </el-col>
        <el-col :span="6">
          <div class="summary">小组成员</div>
          <div class="list_person">

            <div v-for="(item, index) in personList" :key="index" class="person_item"
                 :class="selectStudentId === item['StudentID'] ? 'person_selected' : ''"
                 @click="selectStudentId=item['StudentID']">
              <img :src="defaultPhotoImg" alt=""/>
              <span>{{item['StudentName']}}</span>
              <div>
                <span>{{item['TotalScore']}}分</span>
                <span>{{item['TotalGood']}}赞</span>
              </div>
            </div>

          </div>
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>

<script>
  import commonTable from "@/components/commonTable";
  import imgPhoto from "./../../../../../../static/boy_img.png";
  import { formatDate } from "@/utils/utils";
  import { ContentType, ModulePicture } from "@/utils/enum";

  export default {
    name: "GroupDiscussion",
    components: { commonTable },
    props: {
      groupList: {
        type: Array,
        default: () => {
          return [];
        }
      }
    },
    data() {
      return {
        course: JSON.parse(sessionStorage.getItem("course")),
        courseID: sessionStorage.getItem("courseID"),

        discussSubjectList: [],
        discussSubjectLoading: false,

        subjectList: [],
        tableColumns: [
          { prop: "groupName", label: "小组名称", width: 300 },
          { prop: "good", label: "获赞" },
          { prop: "score", label: "总分" },
          { prop: "oprate", label: "操作", slot: true, width: 240 }
        ],

        dialogVisible: false,
        dataInfo: {},
        dataList: [],
        score: 0,
        good: 0,
        defaultPhotoImg: imgPhoto,
        contentTypeDefault: ContentType.Text,

        personList: [],
        personLoading: false,
        selectStudentId: "",
      }
    },
    methods: {
      //获取图片
      getPicture(filename, fileType) {
        return this.$api.InteractDownload + "?fileName=" + filename + "&fileType=" + fileType + "&platFormType=1";
      },
      initSelect() {
        this.$nextTick(() => {
          if (this.groupList && this.groupList.length > 0) {
            this.getDiscussSubjectList();
          }
        });
      },
      //获取小组讨论题目
      getDiscussSubjectList() {
        this.discussSubjectList = [];
        this.discussSubjectLoading = true;
        this.subjectList = [];
        this.$http
          .get(this.$api.GetStudentGSDatas, {
            courseID: this.courseID,
            studentID: this.$http.getStu().StudentID
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.discussSubjectList.push({
                  ...item,
                  img: this.getPicture(item["Key"]["GSContent"], ModulePicture.GroupSubject)
                });

                let subject = {
                  type: item["Key"]["GSForm"],
                  time: item["Key"]["GSTime"],
                  content: item["Key"]["GSContent"],
                  gsSort: item["Key"]["GSSort"],
                  createTime: item["Key"]["CreateTime"],
                  img: item["Key"]["GSForm"] === 1 ? "" : this.getPicture(item["Key"]["GSContent"], ModulePicture.GroupSubject),
                  state: item["Key"]["State"] === 1 ? "进行中" : "结束讨论",
                };
                let groupInfo = {
                  subjectId: item["Key"]["GSID"],
                  groupId: item["Key"]["GroupID"],
                  groupName: item["GroupName"],
                  good: item["TotalGood"],
                  score: item["TotalScore"],
                };
                let index = this.subjectList.findIndex(r => JSON.stringify(r.subject) === JSON.stringify(subject));
                if (index > -1) {
                  this.subjectList[index].groupList.push(groupInfo);
                } else {
                  this.subjectList.push({ subject, groupList: [groupInfo] })
                }
              });
            }
            this.discussSubjectLoading = false;
          }).catch(e => {
          this.discussSubjectLoading = false;
        });
      },

      formatDate(dateValue, format) {
        return formatDate(dateValue, format);
      },

      viewDetail(row, subject) {
        this.dataInfo = subject;
        this.good = row.good || 0;
        this.score = row.score || 0;
        this.getDiscussionDetail(row);
        this.getGroupMember(row);
        this.dialogVisible = true;
      },
      getDiscussionDetail(row) {
        this.dataList = [];
        this.$http.get(this.$api.GetGSAndGDDatas, { gsid: row.subjectId })
          .then(res => {
            if (res.Result) {
              if (res["GAData"] && res["GAData"].length > 0) {
                res["GAData"].forEach(item => {
                  let value = {
                    id: item["Key"]["GDID"],
                    subjectId: item["Key"]["GSID"],
                    studentId: item["StudentID"],
                    name: item["StudentName"],
                    headImg: imgPhoto,
                    type: item["Key"]["GDForm"],
                    postImg: item["Key"]["GDForm"] === ContentType.Picture ? this.getPicture(item["Key"]["GDContent"], ModulePicture.GroupDiscuss) : "",
                    postContent: item["Key"]["GDForm"] === ContentType.Picture ? "" : item["Key"]["GDContent"],
                    time: this.formatDate(item["Key"]["SubmitTime"], "hh:mm:ss"),
                    thumb: item["Key"]["GDGood"],
                    isThumb: 0,
                    score: item["Key"]["GDScore"]
                  };
                  this.dataList.push(value);
                });
              }
            }
          });
      },
      getGroupMember(row) {
        this.personLoading = true;
        this.personList = [];
        this.$http.get(this.$api.GetGroupMemberRanking, {
          groupID: row.groupId,
          gsid: row.subjectId,
        }).then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.personList.push(item);
            });
            this.selectStudentId = res.Datas[0]["StudentID"];
          }
          this.personLoading = false;
        }).catch(e => {
          this.personLoading = false;
        });
      },
      preview() {

      }
    },
    watch: {
      groupList(newVal, oldVal) {
        this.initSelect();
      }
    }
  }
</script>
<style lang="less" scoped>
  .list_card {
    text-align: center;

    /deep/ .el-card__header {
      padding: 0;

      .card_title {
        display: flex;
        justify-content: space-between;
        padding: 12px 20px;
        font-size: 15px;
      }
    }

    .card_line {
      display: flex;
      justify-content: space-between;
      font-size: 14px;
    }

    .card_img {
      text-align: center;
      height: 100px;

      img {
        max-width: 250px;
        max-height: 100px;
        padding: 10px;
      }
    }

  }

  .table_img {
    max-width: 300px;
    max-height: 100px;
  }

  .summary {
    padding: 0 10px;
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;

    font-size: 14px;
    color: rgba(153, 153, 153, 1);
    line-height: 1.5;

    span {
      font-size: 15px;
      color: rgba(240, 122, 35, 1);
    }
  }

  .chat {
    padding: 0 5px;
    text-align: left;
    width: 100%;
    max-height: 600px;
    overflow-y: auto;
    background: rgba(10, 10, 10, 0.1);
    border: 1px solid #f9f9f9;

    .chat_title {
      display: flex;
      align-items: flex-start;
      margin: 10px 0;

      font-size: 15px;
      color: rgba(51, 51, 51, 1);

      .chat_title_left {
        min-width: 50px;
      }

      .chat_img {
        padding-top: 5px;
      }
    }

    .chat_content {
      .chat_item {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;

        .chat_news {
          .chat_news_name {
            font-size: 12px;
            line-height: 2.5;
            color: #409EFF;
          }

          .chat_news_cont {
            padding: 6px 5px 6px 18px;
            font-size: 14px;
            line-height: 1.5;

            text-align: left;
            word-break: break-all;
            /*border: 1px solid #777;*/
            /*border-radius: 7px;*/

            img {
              max-width: 45vw;
              max-height: 25rem;
            }
          }

          .chat_comment {
            padding-left: 15px;
            color: #F56C6C;
          }
        }
      }

      .checked_item {
        .chat_news {
          .chat_news_name {
            font-weight: bolder;
            color: #67C23A;
          }

          .chat_news_cont {
            color: #777;
          }
        }
      }
    }
  }

  .list_person {
    max-height: 600px;
    overflow-y: auto;

    .person_item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 5px;
      line-height: 40px;

      &:hover {
        background-color: #f5f7fa;
      }

      img {
        max-width: 25px;
        max-height: 25px;
        border-radius: 50%;
      }
    }
  }

  .person_selected {
    background-color: #f1f1fa;
  }
</style>



